<template>
  <div class="all">
    <vue-markdown :source="essay"></vue-markdown>
    <div style="text-align:center;">
    <span class="fa fa-heart fa-lg animated" style="color:red;font-size:40px;cursor:pointer;margin-top:50px;"   v-on:click="good"   v-on:mouseenter="show =!show" v-bind:class="{bounceIn:show}" >&nbsp;{{goods}}</span>
    <transition 
                         name="custom-classes-transition"
                         enter-active-class="animated flipInX"
                         leave-active-class="animated flipOutX" mode="out-in">
    <span style="font-size:30px;color:crimson;display:inline-block;position:absolute;padding-left:10px;margin-top:-6px;" v-show="oneshow"> + 1</span>
      </transition>
      </div>
      
    <div class="comment">
      <div class="inputCom">
        <p>想听你说话</p>
      <textarea name="text"></textarea>
        <input type="button" value="提交评论" v-on:click="outComm">
        <h3>评论列表</h3>
        <hr  style="width:95%;border-color:cadetblue" />
        <div class="com" v-for="(item,index) in allCom">
          <div class="com-left"></div>
          <div class="com-body">
             <span style="color:darkslategrey;font-weight:900;font-size:20px;margin-left:40px;margin-right:20px;"><i class="fa fa-user" style="margin-right:10px"></i>{{item.username}}</span>
             <span style="color:gray;font-weight:500;">{{item.creat_time | formats}}</span>
             <p>{{item.comments}}</p>
          </div>
          <hr />
        </div>
        </div>

    </div>
       <transition 
                         name="custom-classes-transition"
                         enter-active-class="animated bounceIn"
                         leave-active-class="animated bounceOut" mode="out-in" >
            <div class="Session" v-show="comment">
              <label for="" >昵称</label>
              <input type="text" v-model="userId" name="user">
              <label for="" >邮箱</label>
              <input type="text" v-model="userMail" name="mail">
              <label for="">网址</label>
              <input type="text" v-model="userWeb" name="web">
              <input type="button" value="发表评论" v-on:click="SendUser">
              <input type="button" value="关闭评论" style="float:right;" v-on:click="comment =!comment">
        </div>
         </transition>
    </div>
</template>
<script>
  import axios from "axios";
  import moment from 'moment';
  import router from '.././router';
  import VueMarkdown from 'vue-markdown';

  export default {
    props: ["sourceDedata"],
    data() {
      return {
        count: null,
        show: false,
        oneshow: false,
        goods: '',
        comment: false,
        commentLen: null,
        userId: null,
        userMail: null,
        userWeb: null,
        allCom: null,
        Isgood: false,
        essay: ''
      }
    },
    created() {
      var Id = this.$route.path.slice(9, 10) + this.$route.path.slice(11, 12);
      var url = '/content/' + this.$route.path.slice(9, 10) + '/' + this.$route.path.slice(11, 12);
      var essayUrl = '/content/' + this.$route.path.slice(9, 10) + '/' + this.$route.path.slice(11, 12) + '/' + 'essay';
      var comments = url + '/' + "comments";
      var self = this;
      Promise.all(
        [this.$http.get(url), this.$http.get(comments), this.$http.get("/Login"), this.$http.get(essayUrl)]
      ).then(res => {
        this.essay = res[3].body[0].content;
        this.allCom = res[1].body;
        this.commentLen = this.allCom.length;
        console.log(this.commentLen);
        this.goods = res[0].body.rows[0].good;
        this.Isgood = res[0].body.Isgood[Id];
        console.log(self.essay);
        console.log(res[2].body.username);
        if (res[2].body.username != undefined) {
          this.userId = res[2].body.username;
          this.userMail = res[2].body.usermail;
        }
        console.log(this.goods);
        this.allCom.sort(function (b, a) {
          return new Date(a.creat_time).getTime() - new Date(b.creat_time).getTime()
        });
        console.log(this.allCom);
        }).catch(function(reason) {
          console.log(reason);
        })
    },
    components: {
      VueMarkdown
    },
    methods: {
      good() {
        if (!this.Isgood) {
          this.goods = this.goods + 1;
          this.Isgood = true;
          var url = '/content/' + this.$route.path.slice(9, 10) + '/' + this.$route.path.slice(11, 12);
          this.$http.post(url, { good: this.goods }).then(res => {
            console.log("send OK");
          });
          var self = this;
          this.oneshow = true;
          setInterval(function () {
            self.oneshow = false;
            clearInterval();
          }, 500)
        } else {
          alert("您已经参与过投票了！^_^");
        }
      },
      outComm() {
        this.comment = true;
      },
      SendUser() {
        this.commentLen = this.commentLen + 1;
        var user = $("input[name='user']").val();
        var mail = $("input[name='mail']").val();
        var web = $("input[name='web']").val();
        var text = $("textarea[name='text']").val();
        var artId = this.$route.path.slice(9, 10) + this.$route.path.slice(11, 12);
        var creatTime = new Date();
        var self = this;
        var url = '/content/' + this.$route.path.slice(9, 10) + '/' + this.$route.path.slice(11, 12);
        var comments = url + '/' + "comments";
        this.$http.post("/Login", {
          user: user,
          mail: mail,
          web: web,
          artId: artId,
          text: text,
          commentLen: self.commentLen,
          creat_time: creatTime
        }).then(res => {
          self.comment = false;
          $("textarea[name='text']").val("");
          this.$http.get(comments).then((res) => {
            this.allCom = res.body;
            this.allCom.sort(function (b, a) {
              return new Date(a.creat_time).getTime() - new Date(b.creat_time).getTime()
            });
            console.log("Updata List OK");
            console.log(res.body[0].username);
          });
        })
      },
    },
    filters: {
      formats: function (value) {
        value = moment(value).format("YYYY-MM-DD HH:mm:ss");
        return value;
      }
    }
  }
</script>
<style>
  div.all{
    background-color:rgba(255, 255, 255,0.6);
    border-radius:20px;
    margin:0 auto;
    width:80%;
    padding:50px;
    margin-top:100px;
  }
  div.detail{
    text-align:center;

  }
    div.comment{
    text-align:center;
    overflow:hidden;
    margin-top:20px;
  }
  div.inputCom{
    margin:40px;
    border:1px solid;
    border-color:steelblue;
    border-radius:10px;
    
  }
  .inputCom p{
    display:inline-block; 
    float:left;
    width:100%;
    font-weight:700;
    color:darkcyan;
    background-color:rgba(0, 148, 255,0.4);
    text-align:left;
    padding-left:10px;
    padding-top:10px;
    padding-bottom:10px;
  }
  .inputCom textarea{
    margin-top:10px;
    width:95%;
    height:200px;
    background-color:rgba(255, 255, 255,0.4);
    border:1px solid steelblue;
    border-radius:10px;
  }
  .inputCom input{
    position:relative;
    border-radius:5px;
    border:1px solid steelblue;
    background-color:steelblue;
    color:white;
    padding:10px;
    margin-bottom:20px;
    left:43%;
  }
  .inputCom h3{
      margin-left:-85%;
      color:cornflowerblue;
      
  }
  .Session{
    position:fixed;
    //border:1px red solid;
    background-color:rgba(255,255,255,0.8);
    border-radius:8px;
    top:25%;
    left:40%;
    width:20%;
  }
  .Session input{
     margin:6px;
  }
  .Session label{
    margin:10px;
  }
  .Session input[type=button]{
    background-color:cadetblue;
    border-radius:5px;
    padding:10px;
    border:none;
    color:white;
  }

  .com-body{
    text-align:left;
    height:50px;
  }
  .com-body p{
    font-size:large;
    background-color:transparent;
    color:dimgrey;
    margin-left:50px;
  }
  .com hr{
    width:95%;
    border-top:1px solid grey;
  }
  .com:last-child hr{
    border-top:none;
  }





</style>



